"use client";

import React, { createContext, useContext, useState, useEffect } from 'react';
import Cookies from 'js-cookie';

type AuthContextType = {
  isAuthenticated: boolean;
  isInitialized: boolean;
  login: (username: string, password: string) => boolean;
  logout: () => void;
  user: string | null;
};

const AuthContext = createContext<AuthContextType | undefined>(undefined);

export const ADMIN_USERNAME = 'admin';
export const ADMIN_PASSWORD = 'echoit1111@';

// Simple check if we're in a browser environment
const isBrowser = typeof window !== 'undefined';

// Auth storage key
const AUTH_STORAGE_KEY = 'echoit_admin_auth';

export const AuthProvider: React.FC<{ children: React.ReactNode }> = ({ children }) => {
  const [isAuthenticated, setIsAuthenticated] = useState<boolean>(false);
  const [user, setUser] = useState<string | null>(null);
  const [isInitialized, setIsInitialized] = useState<boolean>(false);

  useEffect(() => {
    let isMounted = true;

    // Check if user is already logged in via cookie or localStorage
    const initAuth = async () => {
      try {
        // First try localStorage (works better in preview environments)
        if (isBrowser) {
          const storedAuth = localStorage.getItem(AUTH_STORAGE_KEY);
          if (storedAuth) {
            const userData = JSON.parse(storedAuth);
            if (userData.username === ADMIN_USERNAME && isMounted) {
              setIsAuthenticated(true);
              setUser(userData.username);
              setIsInitialized(true);
              return;
            }
          }
        }

        // Fallback to cookies
        const authCookie = Cookies.get('admin_auth');
        if (authCookie) {
          const userData = JSON.parse(authCookie);
          if (userData.username === ADMIN_USERNAME && isMounted) {
            setIsAuthenticated(true);
            setUser(userData.username);

            // Also set in localStorage for resilience
            if (isBrowser) {
              localStorage.setItem(AUTH_STORAGE_KEY, JSON.stringify({ username: userData.username }));
            }
          }
        }
      } catch (error) {
        console.error('Failed to parse auth data:', error);
        if (isBrowser) {
          Cookies.remove('admin_auth');
          localStorage.removeItem(AUTH_STORAGE_KEY);
        }
      } finally {
        if (isMounted) {
          setIsInitialized(true);
        }
      }
    };

    initAuth();

    return () => {
      isMounted = false;
    };
  }, []);

  const login = (username: string, password: string): boolean => {
    if (username === ADMIN_USERNAME && password === ADMIN_PASSWORD) {
      setIsAuthenticated(true);
      setUser(username);

      const authData = JSON.stringify({ username });

      // Store auth in both cookie and localStorage for resilience
      Cookies.set('admin_auth', authData, {
        expires: 7, // 7 days
        secure: process.env.NODE_ENV === 'production',
        sameSite: 'lax' // Changed from 'strict' to allow preview environments
      });

      if (isBrowser) {
        localStorage.setItem(AUTH_STORAGE_KEY, authData);
      }

      return true;
    }
    return false;
  };

  const logout = () => {
    setIsAuthenticated(false);
    setUser(null);

    if (isBrowser) {
      Cookies.remove('admin_auth');
      localStorage.removeItem(AUTH_STORAGE_KEY);
    }
  };

  return (
    <AuthContext.Provider value={{ isAuthenticated, isInitialized, login, logout, user }}>
      {children}
    </AuthContext.Provider>
  );
};

export const useAuth = (): AuthContextType => {
  const context = useContext(AuthContext);
  if (context === undefined) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};
